/* 
包含多个由action触发去直接更新状态的方法的常量
*/
import { ADD_ITEM,DEL_ITEM,SELECT_ALL,DEL_COMPLETED,RECEIVE_TODOS } from './mutation-types'
// ADD_ITEM

export default {
    INCREMENT(state) {
        state.count++
    },

    DECREMENT(state) {
        state.count--
    },

    [ADD_ITEM] (state,{todo}) {
        state.todos.unshift(todo)
    },

    [DEL_ITEM](state,{index}){
        state.todos.splice(index,1)
    },

    [SELECT_ALL](state,{check}){
        state.todos.forEach(item => {
            item.complete = check
        });
    },

    [DEL_COMPLETED](state){
        state.todos = state.todos.filter(item => !item.complete)
    },

    [RECEIVE_TODOS](state,todos){
        state.todos = todos
    },
}