/*
包含所有基于state的getter计算属性的对象
*/
export default {
    evenOrOdd(state) {
        return state.count % 2 === 0 ? '偶数' : '奇数'  //不需要调用，只需要读取，意思就是不用传值
    },

    // todos总数 
    totalCount(state){
        return state.todos.length
    },

    // 选中的todo数量
    completeNum(state){
        return state.todos.reduce((total,item) => total + (item.complete?1:0),0) 
    },

    // 判断是否全部选中
    isSelectAll(state,getters){
        return getters.totalCount === getters.completeNum && state.todos.length>0
    }
}