/*
包含多个接受组件通知（组件触发），触发mutation间接更新状态
*/
import { ADD_ITEM,DEL_ITEM,SELECT_ALL,DEL_COMPLETED,RECEIVE_TODOS } from './mutation-types'
import storageManage from '../util/storageManage'

export default {
    increment({ commit }) {
        commit('INCREMENT')  //提交mutation
    },

    decrement({ commit }) {
        commit('DECREMENT')  //提交mutation
    },
    // incrementIfOdd({commit}){
    //   commit('INCREMENTIFODD')
    // },

    incrementIfOdd({ commit, state }) {
        if (state.count % 2 === 1) {
            commit('INCREMENT')
        }
    },

    incrementIfAsync({ commit }) {
        // commit('INCREMENTIFASYNC')
        setTimeout(() => {
            commit('INCREMENT')
        }, 100)
    },

    addItem({commit},todo){
        commit(ADD_ITEM,{todo}) // commit()提交对mutation的请求，此处commit的ADD_ITEM传过去的是小写的那个值，而不是大写的字符串
    },

    delItem({commit},index){
        commit(DEL_ITEM,{index}) 
    },

    selectAll({commit},check){
        commit(SELECT_ALL,{check})
    },

    delCompleted({commit}){
        commit(DEL_COMPLETED)
    },

    // 异步获取todos并更新状态
    reqTodos({commit}){
        // 模拟从服务器获取数据
        setTimeout( () => {
            // 读取数据
            const todos = storageManage.getTodos()

            // 提交mutation
            commit(RECEIVE_TODOS,todos)
        },1000 )
    },
}